<template>
  <div class="nav">
      <div :class="['nav-left-after',{'nav-left-touch':rouState.touchLeft,'nav-left-after-leave':rouState.leaveLeft}]"></div>
      <div :class="['nav-left-before',{'nav-left-touch':rouState.touchLeft,'nav-left-leave':rouState.leaveLeft}]"></div>
      <div class="nav-left" @touchstart="touchStartLeft" @touchmove="touchEndLeft" @touchend="touchEndLeft" @mouseup="rou">
        <div class="nav-left-icon"></div>
      </div>

      <div class="nav-right" @touchstart="touchStartRight" @touchmove="touchEndRight" @touchend="touchEndRight">
        <!-- <img :src="src" alt=""> -->
      </div>
    <p>{{title}}</p>
  </div>

</template>

<script>
export default {
  props: ['title'],
  data() {
    return {
      rouState: {
        leaveLeft: false,
        touchLeft: false,
        leaveRight: false,
        touchRight: false
      }
    };
  },
  methods: {
    rou() {
      this.$router.goBack();
    },
    touchStartLeft() {
      this.rouState.leaveLeft = false;
      this.rouState.touchLeft = true;
    },
    touchEndLeft() {
      this.rouState.touchLeft = false;
      this.rouState.leaveLeft = true;
    },
    touchStartRight() {
      this.rouState.leaveRight = false;
      this.rouState.touchRight = true;
    },
    touchEndRight() {
      this.rouState.touchRight = false;
      this.rouState.leaveRight = true;
    }
  }
};
</script>

<style lang="less" scoped>
@import '../../style/common';
.nav {
  position: relative;
  width: 100%;
  // -webkit-overflow-scrolling : touch;
  height: 0.88rem;
  background: #fff;
  box-shadow: 0 0.02rem 0.05rem rgba(0, 0, 0, 0.1);
  .nav-left-before,
  .nav-left-after,
  .nav-left {
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 0.3rem;
    position: absolute;
    line-height: 0.6rem;
    left: 0.2rem;
    top: 0.16rem;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    transition: all 0.36s cubic-bezier(0.32, 0.1, 0.32, 1);
    .nav-left-icon {
      border-left: 0.04rem solid;
      border-bottom: 0.04rem solid;
      width: 0.2rem;
      height: 0.2rem;
      margin: 0.18rem 0 0.18rem 0.24rem;
      transform: rotate(45deg);
      border-color: #666;
    }
  }
  .nav-left-before {
    opacity: 0.5;
    background: #dfdfdf;
    transform: scale(0);
  }
  .nav-left-touch {
    transform: scale(1);
    opacity: 0.5;
    transition: all 0.42s cubic-bezier(0.32, 0.1, 0.32, 1);
  }
  .nav-left-leave {
    transform: scale(0);
    opacity: 0;
    transition: none;
  }
  .nav-left-after {
    opacity: 0.5;
    background-color: transparent;
    transform: scale(1);
    transition: none;
  }
  .nav-left-after-leave {
    animation: leave 0.82s;
  }
  @keyframes leave {
    0% {
      background-color: #dfdfdf;
    }
    100% {
      background-color: transparent;
    }
  }
  .nav-right {
    width: 0.32rem;
    height: 0.32rem;
    position: absolute;
    right: 0.24rem;
    top: 0.25rem;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    transition: all 0.42s cubic-bezier(0.32, 0.1, 0.32, 1);
    .nav-right-icon-1,
    .nav-right-icon-2,
    .nav-right-icon-3 {
      width: 0.06rem;
      height: 0.06rem;
      margin: 0.06rem;
      background-color: #666;
      border-radius: 0.03rem;
      transform: rotate(90deg);
    }
    .nav-right-icon-1 {
      margin-top: 0.02rem;
    }
  }
}

.nav p {
  text-align: center;
  font-size: 0.32rem;
  color: #333;
  height: 0.88rem;
  line-height: 0.88rem;
  user-select: none;
}
</style>



